import React, { useState, useContext } from 'react';
import UserCarContext from '../../context/usercar/UserCarContext';
import M from 'materialize-css/dist/js/materialize.min.js';

const AddCarModal = () => {
  const [carNo, setcarNo] = useState('');
  const [carColor, setcarColor] = useState('');
  const [carSlot, setcarSlot] = useState('');
 // const [carAmount, setcarAmount] = useState(10);

  const userCarContext = useContext(UserCarContext);
  const {storeUserCarData,carData} = userCarContext;

  const onSubmit = (e) => {
    e.preventDefault();
    if (carNo === '' || carColor === '' || carSlot === '') {
      M.toast({ html: 'Please enter the all input field details' });
    } else {
      let dupliSlot = carData.filter(c=>c.carSlot === carSlot);
      if(dupliSlot.length > 0){
        M.toast({ html: `Please select another slot as Entered car slot - ${carSlot} is already present`});
         // Clear Fields
         setcarNo('');
         setcarColor('');
         setcarSlot('');
      }else if(carSlot < 0 ){
        M.toast({ html: `Please select positive value as car slot number!!`});
        // Clear Fields
        setcarNo('');
        setcarColor('');
        setcarSlot('');
      }else{
        storeUserCarData({
          carNo,
          carColor,
          carSlot,
          carAmount:10,
          date: new Date() 
          });
          M.toast({ html: `Car No - ${carNo} with ${carColor} colour and slot number - ${carSlot} was added in parking system.` });
          // Clear Fields
          setcarNo('');
          setcarColor('');
          setcarSlot('');
      }



    }
  };

  return (
    <div id='add-user-park-modal' className='modal'>
      <div className='modal-content'>
          <h4>Enter New Car Parking Details</h4>
          <div className='row'>
            <div className='col s12 m4'>
            <div className='input-field'>
              <input
                type='text'
                name='carNo'
                value={carNo}
                maxLength='20'
                onChange={e => setcarNo(e.target.value)}
                required
              />
              <label htmlFor='carNo' className='active'>
                Car No<span className="red-text">*</span>
              </label>
            </div>
          </div>
          <div className='col s12 m4'>
            <div className='input-field'>
              <input
                type='text'
                name='carColor'
                value={carColor}
                onChange={e => setcarColor(e.target.value)}
                required
              />
              <label htmlFor='carColor' className='active'>
                Car Colour<span className="red-text">*</span>
              </label>
            </div>
          </div>
          <div className='col s12 m4'>
            <div className='input-field'>
              <input
                type='number'
                name='carSlot'
                value={carSlot}
                maxLength='2'
                onChange={e => setcarSlot(e.target.value)}
                required
              />
              <label htmlFor='carSlot' className='active'>
                Car Slot No<span className="red-text">*</span>
              </label>
            </div>
          </div>
        </div>
      </div>
      <div className='modal-footer'>
        <div className='row'>
          <div className='col s12 m4 push-m4 pull-m4'>
            <a
              href='#parking-tool'
              onClick={onSubmit}
              className='modal-close waves-effect blue waves-light btn'
            >
              Park
            </a>
          </div>
        </div>
      </div>
    </div>
  );
};


export default AddCarModal